@import url("https://fonts.googleapis.com/css2?family=Bree+Serif&family=Space+Grotesk&display=swap");
* {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

/* --------------------------------

xBackground

-------------------------------- */
body::before,
body,
body::after {
  width: 100%;
  height: 100%; }

body {
  z-index: 0;
  background: #000 url("./assets/stars.png") repeat top center; }

body::before {
  content: '';
  position: fixed;
  display: block;
  z-index: 1;
  background: transparent url("./assets/twinkling.png") repeat top center;
  animation: move-twink-back 200s linear infinite; }

body::after {
  content: '';
  position: fixed;
  display: block;
  z-index: 2;
  background: transparent url("./assets/clouds.png") repeat top center;
  animation: move-clouds-back 200s linear infinite; }

body > * {
  z-index: 3;
  position: relative; }

@keyframes move-twink-back {
  from {
    background-position: 0 0; }
  to {
    background-position: -10000px 5000px; } }

@keyframes move-clouds-back {
  from {
    background-position: 0 0; }
  to {
    background-position: 10000px 0; } }

/* end background */
/* containers */
.page-wrapper {
  overflow-x: hidden; }

.page-wrapper,
.intro,
.main,
.sidebar > .wrapper {
  display: flex;
  flex-direction: column; }

.intro,
.main,
.sidebar > .wrapper {
  align-items: center;
  justify-content: center; }

.intro > *,
.main > *,
.sidebar > .wrapper > * {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.sidebar h3 {
  margin-bottom: 0; }

.sidebar ul {
  margin-top: 0.5em; }

.intro header {
  justify-content: flex-end;
  width: calc(100vw - (100vw - 100%));
  height: 100vw;
  border-radius: 50%;
  padding-bottom: 15%;
  margin-top: -50vw;
  background-color: orange; }

.summary {
  width: 68vw;
  height: 68vw;
  border-radius: 50%;
  padding: 14.65%;
  background-color: brown; }

.preamble {
  width: 125vw;
  height: 125vw;
  border-radius: 50%;
  padding: 14.65%;
  background-color: yellow; }

.preamble > * {
  max-width: 100vw;
  padding-left: 2rem;
  padding-right: 2rem; }

.explanation {
  order: 4;
  width: 130vw;
  height: 130vw;
  border-radius: 50%;
  padding: 14.65%;
  background-color: green; }

.explanation > * {
  max-width: 100vw;
  padding-left: 2rem;
  padding-right: 2rem; }

.participation {
  order: 1;
  width: 150vw;
  height: 150vw;
  border-radius: 50%;
  padding: 14.65%;
  background-color: red; }

.participation > * {
  max-width: 100vw;
  padding-left: 2rem;
  padding-right: 2rem; }

.benefits {
  order: 2;
  width: 90vw;
  height: 90vw;
  border-radius: 50%;
  padding: 14.65%;
  background-color: darkorange; }

.requirements {
  order: 3;
  width: 250vw;
  height: 250vw;
  border-radius: 50%;
  background-color: grey; }

.requirements > * {
  max-width: 100vw;
  padding-left: 2rem;
  padding-right: 2rem; }

.design-selection {
  width: 100vw;
  height: 100vw;
  border-radius: 50%;
  padding: 14.65%;
  background-color: teal; }

.design-archives {
  width: 60vw;
  height: 60vw;
  border-radius: 50%;
  background-color: blue; }

.zen-resources {
  width: 70vw;
  height: 70vw;
  border-radius: 50%;
  background-color: lightblue; }

#zen-supporting footer {
  position: absolute;
  bottom: 0;
  background-color: #94999e;
  width: 100%;
  height: 100px;
  flex-direction: row;
  justify-content: space-around; }

@media all and (min-width: 50em) {
  body {
    margin-bottom: .5em; }
  .intro header {
    height: 60vw;
    margin-top: -25%;
    padding-bottom: 10%;
    margin-bottom: .5em; }
  .summary {
    width: 58vw;
    height: 58vw;
    padding: 10%;
    margin-bottom: .5em;
    margin-right: 35%; }
  .preamble {
    width: 100vw;
    height: 100vw;
    padding: 10%;
    margin-bottom: .5em; }
  .preamble > * {
    max-width: 90vw; }
  .explanation {
    width: 120vw;
    height: 120vw;
    padding: 10%;
    margin-bottom: .5em; }
  .participation {
    padding: 5% 14.65% 5% 14.65%;
    width: 130vw;
    height: 130vw;
    margin-bottom: .5em; }
  .benefits {
    padding: 5% 14.65% 5% 14.65%;
    width: 75vw;
    height: 75vw;
    margin-bottom: .5em; }
  .requirements {
    padding: 10%;
    width: 160vw;
    height: 160vw;
    margin-bottom: .5em; }
  .design-selection {
    padding: 5%;
    width: 50vw;
    height: 50vw;
    margin-top: .5em; }
  .design-archives {
    padding: 5%;
    width: 45vw;
    height: 45vw;
    margin-left: 45%; }
  .zen-resources {
    padding: 5%;
    width: 50vw;
    height: 50vw;
    margin: -10% 45% 0 10%; } }

@media all and (min-width: 73em) {
  .intro header {
    max-width: 73em;
    height: 73em;
    padding-bottom: 5%; }
  .summary {
    align-self: flex-start;
    max-width: 40em;
    max-height: 40em; }
  .preamble {
    align-self: flex-end;
    max-width: 55em;
    max-height: 55em;
    margin-top: -10%; }
  .participation {
    align-self: flex-start;
    max-width: 65em;
    max-height: 65em; }
  .benefits {
    align-self: flex-end;
    max-width: 50em;
    max-height: 50em; }
  .requirements {
    max-width: 90em;
    max-height: 90em; }
  .explanation {
    max-width: 60em;
    max-height: 60em;
    align-self: flex-start; }
  .design-selection {
    max-width: 55em;
    max-height: 55em;
    align-self: flex-end; }
  .design-archives {
    max-width: 50em;
    max-height: 50em;
    align-self: flex-start; }
  .zen-resources {
    max-width: 20em;
    max-height: 20em;
    margin-bottom: 10%; } }

h1,
h2 {
  font-family: "Bree Serif", serif;
  color: #94999e; }

h1 {
  font-size: 2.4rem;
  font-weight: 300;
  line-height: 1.2;
  margin: 0 0 0.4em; }
  @media only screen and (min-width: 0em) {
    h1 {
      font-size: 3.2rem; } }
  @media only screen and (min-width: 50em) {
    h1 {
      font-size: 4rem; } }

h2 {
  font-weight: bold;
  text-transform: uppercase;
  margin: 1em 0; }
  @media only screen and (min-width: 0em) {
    h2 {
      font-size: 1.8rem;
      margin: 1em 0 1.4em; } }

section > h2::before {
  /* number before each section title */
  color: #edb831; }

p {
  font-size: 1.4rem;
  line-height: 1.4;
  color: #d7d9db; }
  p a {
    color: #5f86b1;
    text-decoration: underline; }
  @media only screen and (min-width: 0em) {
    p {
      font-size: 1.6rem;
      line-height: 1.6; } }

figcaption {
  color: #94999e; }
